import { useEffect, useState } from "react";
import store from "../../../Redux/Store";
import web from "../../../Services/WebApi";
import Circle from "../../SharedArea/Circle/Circle";
import notify from "../../SharedArea/Notify";
import "./TodoTotal.css";


function TodoTotal(): JSX.Element {

    let [num, setNum] = useState(store.getState().taskReducer?.tasks?.length);
console.log("todototal 1:"+num);
    useEffect(() => {
        if (num === 0) {
            web.countTasks()
                .then(res => {
                    setNum(res.data);
                    notify.success('total : ' + res.data);
                })
                .catch(err => notify.error(err.message));
        }
        return store.subscribe(() => {
            setNum(store.getState().taskReducer?.tasks?.length); // Will let us notify
            console.log("store updated (num):"+num);
            
        });
    }, [num]);





    return (
        <div className="TodoTotal">
            <Circle num={num}></Circle>
        </div>
    );
}
export default TodoTotal;
