import "./AddTodo.css";
import { useForm } from "react-hook-form";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { TodoModel } from "../../../models/Todo";
import axios from "axios";

import globals from "../../../Services/globals";
import notify from "../../SharedArea/Notify";
import { useNavigate } from "react-router-dom";
import web from "../../../Services/WebApi";
import store from "../../../Redux/Store";
import { taskAddedAction } from "../../../Redux/TasksAppState";
function AddTodo(): JSX.Element {
  const navigate=useNavigate();
  const schema = yup.object().shape({
    caption: yup.string().required("caption is required"),
    info: yup.string().required("info is required"),
    classification: yup.string().required("classification is required"),
    dueDate: yup
      .date()
      .min(new Date(), "past task? you are better then that")
      .default(new Date())
      .typeError("You must specify a dueDate")
      .required("dueDate is required")
      .nullable()
      .default(() => new Date()),
  });
  const { register, handleSubmit, formState: { errors, isDirty, isValid } } =
        useForm<TodoModel>({ mode: "all", resolver: yupResolver(schema) });

 const submit=async (todo: TodoModel)=>{
  
   web.addTask(todo)
   .then(res=>{notify.success('new task successfully created');
  navigate('/tasks');
  store.dispatch(taskAddedAction(res.data));
  })
    .catch(err=>{notify.error('an error append:'+err.message)});
 }       
  return (
    <div className="AddTodo flex-col">
      <form onSubmit={handleSubmit(submit)} className="flex-col">
        <label htmlFor="caption">caption</label>
        <input {...register("caption")}type="text" placeholder="caption" id="caption" />
        <span>{errors.caption?.message}</span>
        <label htmlFor="info">info</label>
        <input {...register("info")} type="text" placeholder="info" id="info" />
        <span>{errors.info?.message}</span>
        <label htmlFor="classification">classification</label>
        <input {...register("classification")} type="classification" placeholder="classification" id="classification"/>
        <span>{errors.classification?.message}</span>
        <label htmlFor="dueDate">dueDate</label>
        <input {...register("dueDate")} type="datetime-local" placeholder="dueDate" id="dueDate" />
       <span>{errors.dueDate?.message}</span>
        <button disabled={!isValid}>Add</button>
      </form>
    </div>
  );
}

export default AddTodo;
