import "./Login.css";
import { useForm } from "react-hook-form";
import * as yup from "yup";
import { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { yupResolver } from "@hookform/resolvers/yup";
import { CredentialsModel, LoginModel, UserModel } from "../../../models/Welcome";
import web from "../../../Services/WebApi";
import notify from "../../SharedArea/Notify";
import { Navigate } from "react-router-dom";
import store from "../../../Redux/Store";
import { loginAction } from "../../../Redux/UserAppState";
function Login(): JSX.Element {
  const navigate = useNavigate();
  const schema = yup.object().shape({
    email: yup
      .string()
      .required("email is required")
      .email("Invalid Email Pattern"),
    password: yup.string().required("password is required"),
  });

  const {
    register,
    handleSubmit,
    formState: { errors, isDirty, isValid },
  } = useForm<LoginModel>({ mode: "all", resolver: yupResolver(schema) });
  const loginUser = async (model: LoginModel) => {
    const credentials = new CredentialsModel();
    credentials.email = model.email;
    credentials.password = model.password;

    console.log("going to send to remote server..." + credentials);

    web
      .login(credentials)
      .then((res) => {
        notify.success("login successfully");
        const model: UserModel = res.data;
        
        console.log(model);
        store.dispatch(loginAction(model));
        navigate("/tasks");
      })
      .catch((err) => {
        console.log(err);
        notify.error(err.value);
      });
  };

  return (
    <div className="Login flex-col">
      {/* Step 9 - handleSubmit your form  */}
      <form onSubmit={handleSubmit(loginUser)} className="flex-col">
        <label htmlFor="email">Email</label>
        <input
          {...register("email")}
          type="email"
          placeholder="email"
          id="email"
        />
        <span>{errors.email?.message}</span>
        <label htmlFor="password">Password</label>
        <input
          {...register("password")}
          type="password"
          placeholder="password"
          id="password"
        />
        <span>{errors.password?.message}</span>

        <button className="button-success" disabled={!isValid}>
          Login
        </button>
      </form>
    </div>
  );
}

export default Login;
