import axios from "axios";
import { error } from "console";
import { Notyf } from "notyf";
import { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import store from "../../../Redux/Store";
import { taskDeletedAction } from "../../../Redux/TasksAppState";
import globals from "../../../Services/globals";
import web from "../../../Services/WebApi";
import notify from "../../SharedArea/Notify";
import "./DeleteTodo.css";

function DeleteTodo(): JSX.Element {
    const params = useParams();
    const navigate=useNavigate();
    const taskId = +(params.id || 0);
    const[id,setId]=useState(taskId);
    const  no=()=>{
        navigate("/tasks");
    }
    const yes=()=>{
            
       web.deleteTask(id)
        .then(res=>{notify.success('task '+id +' was deletes sucsesfuly');
        navigate("/tasks");
        store.dispatch(taskDeletedAction(id))
    })
        .catch(err=>{notify.error(err.message);
          navigate("/tasks");
        });
        
    }
    return (
        <div className="DeleteTodo flex-col">
			<h1>Delete task</h1>
           <h3>are you sure want do delete task number {id}?</h3>
            <div>
            <button onClick={yes}>YES</button>
            <button onClick={no}>NO</button>
            </div>
       
        </div>
    );
}

export default DeleteTodo;
