
import { useEffect, useState } from "react";
import { AiOutlineAppstoreAdd } from "react-icons/ai";
import { Link } from "react-router-dom";
import { TodoModel } from "../../../models/Todo";
import store from "../../../Redux/Store";
import { tasksDownloadedAction } from "../../../Redux/TasksAppState";
import web from "../../../Services/WebApi";
import notify, { SccMsg } from "../../SharedArea/Notify";
import NoTasks from "../NoTasks/NoTasks";
import ToDoItem from "../ToDoItem/ToDoItem";

function TodoList(): JSX.Element {

    const [tasks, setTasks] = useState<TodoModel[]>(store.getState().taskReducer.tasks);

    useEffect(() => {
        console.log("status:"+store.getState().taskReducer.tasks?.length);
       
            web.getAllTasks()
                .then((res) => {
                    notify.success('got all the tasks');
                    // Update Component State (Local state)
                 
                    setTasks(res.data);
                    // Update App State (Global State)
                    store.dispatch(tasksDownloadedAction(res.data));
                })
                .catch((err) => {
                    notify.error(err.message);
                });
        
    }, []);

   return (
    <div className="ToDoList flex-col">
        <h2> todo list</h2>
       <Link to="add"><AiOutlineAppstoreAdd size={42} /> </Link> 
    
        <div className="flex-row.none-wrap-list">
        {(tasks.length>0 )? tasks.map(t=><ToDoItem key={t.id} task={t}/>) : <NoTasks/>}
    </div>
    </div>
  );
}


export default TodoList;